import React, { useEffect, useState } from 'react';
import { Grid, Grow, Card, CardContent, Typography, Divider } from '@mui/material';
import { useDarkMode } from '../../context/darkModeContext';
import Stack from '@mui/material/Stack';
import DescriptionIcon from '@mui/icons-material/Description';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import PersonIcon from '@mui/icons-material/Person';
import { useTranslation } from '../../context/i18nContext';





const ContenidoEducacion = () => {
  const [open, setOpen] = useState(false);
  const { darkMode} = useDarkMode();
  const { translate } = useTranslation();
  

  useEffect(() => {
    setTimeout(() => {
      setOpen(true);
    }, 2000);
  }, []);




  return (
    <Grid container justifyContent="center" alignItems="center" spacing={2}>
      <Grow in={open} style={{ transformOrigin: '0 0 0' }} {...(open ? { timeout: 1000 } : {})}>
        <Grid item md={9} xs={12}>
          <Card sx={{backgroundColor: darkMode ? "#2E2E2E": "white", color: darkMode ? "white": "grey"}}>
            <CardContent sx={{ display: 'flex', justifyContent: 'left' }}>
              <Stack
                direction={{ xs: 'column', sm: 'row' }}
                spacing={{ xs: 1, sm: 2, md: 4 }}
                sx={{width: "100%"}}
              >
                <Grid item md={6} xs={12}>
                  <Typography variant='h6' sx={{color: darkMode ? "white": "black"}}>ZerviZ</Typography>
                  <Divider sx={{width: "95%", marginBottom: "20px"}}/>
                  <Typography variant='subtitle2'><PersonIcon fontSize='4px' sx={{marginRight: "10px"}}/>{translate("ZervizCargo")} </Typography>
                  <Typography variant='subtitle2'><CalendarMonthIcon fontSize='4px' sx={{marginRight: "10px"}}/>2021 - 2023</Typography>
                  <Typography variant='subtitle2' sx={{textAlign: "justify"}}><DescriptionIcon fontSize='4px' sx={{marginRight: "10px"}}/>{translate("ZervizDescripcion")}</Typography>
                </Grid>
                <Grid item md={6} xs={12}>
                  <Typography variant='h6' sx={{color: darkMode ? "white": "black"}}>Kamaleon</Typography>
                  <Divider sx={{width: "95%", marginBottom: "20px"}}/>
                  <Typography variant='subtitle2'><PersonIcon fontSize='4px' sx={{marginRight: "10px"}}/>{translate("KamaelonCargo")}</Typography>
                  <Typography variant='subtitle2'><CalendarMonthIcon fontSize='4px' sx={{marginRight: "10px"}}/>2020 - 2021</Typography>
                  <Typography variant='subtitle2' sx={{textAlign: "justify"}}><DescriptionIcon fontSize='4px' sx={{marginRight: "10px"}}/>{translate("KamaleonDescripcion")}</Typography>
                </Grid>
          
              </Stack>
            </CardContent>
          </Card>
        </Grid>
      </Grow>
    </Grid>
  );
}

export default ContenidoEducacion;
