import { Grid } from '@mui/material'
import React from 'react'
import ContenidoLaboral from './ContenidoLaboral'
import TituloLaboral from './TituloLaboral'

const Laboral = () => {
  return (
    <>
    <TituloLaboral/>
    <Grid container marginTop={6} >
      <ContenidoLaboral/>
    </Grid>
    </>
  )
}

export default Laboral
