import { Divider, Fade, Grid, Grow, Modal, Paper, Tooltip, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import "../../App.css"
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import { useDarkMode } from '../../context/darkModeContext';
import { useTranslation } from '../../context/i18nContext';
import GitHubIcon from '@mui/icons-material/GitHub';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';


const CountriesApp = () => {
  const [open, setOpen] = useState(false)
  const { darkMode} = useDarkMode();
  const { translate } = useTranslation();
  const [openComponente, setOpenComponente]= useState(false)
  const imagen = process.env.PUBLIC_URL + "/img/png/CountriesApp.PNG"

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    setTimeout(() => {
        setOpenComponente(true) 
    }, 1000);
    
  }, [])
  
  return (
    <>
      <Grow in={openComponente} style={{ transformOrigin: '0 0 0' }} {...(openComponente ? { timeout: 1000 } : {})}>
        <Grid item md={3} xs={12} sx={{  textAlign: "left"}}>
            <Card  className="zoom-image"  sx={{backgroundColor: darkMode ? "#2E2E2E": "white", color: darkMode ? "white": "black"}}>
                <CardMedia
                    component="img"
                    alt="Countries App"
                    image={imagen}
                />  
                <CardContent >
                    <Typography gutterBottom variant="h5" component="div" >
                    {translate("NombreProyecto_CountriesApp")}
                    </Typography>
                    <Divider sx={{backgroundColor: darkMode ? "#555555": ""}}/>
                </CardContent>
                <CardActions sx={{justifyContent: "center"}}>
                    <Tooltip title="Bootstrap">
                        <img className="zoom-svg" src={process.env.PUBLIC_URL + '/img/svg/bootstrap.svg'} style={{width: "30px"}} alt="Logo" />  
                    </Tooltip>
                    <Tooltip title="Javascript">
                        <img className="zoom-svg" src={process.env.PUBLIC_URL + '/img/svg/js.svg'} style={{width: "30px"}} alt="Logo" />
                    </Tooltip>
                    <Tooltip title="Python">
                        <img className="zoom-svg" src={process.env.PUBLIC_URL + '/img/png/python.png'} style={{width: "30px"}} alt="Logo" />
                    </Tooltip>
                    <Tooltip title="CSS">
                        <img className="zoom-svg" src={process.env.PUBLIC_URL + '/img/svg/css-3.svg'} style={{width: "30px"}} alt="Logo" />
                    </Tooltip>
                    <Tooltip title="Api REST">
                        <img className="zoom-svg" src={process.env.PUBLIC_URL + '/img/png/apiRest.png'} style={{width: "30px"}} alt="Logo" />
                    </Tooltip>
                    <Tooltip title="Jquery">
                        <img className="zoom-svg" src={process.env.PUBLIC_URL + '/img/png/jquery.png'} style={{width: "30px"}} alt="Logo" />
                    </Tooltip>
                </CardActions>
                <CardActions>
                    <Button color="success" onClick={handleOpen} variant="contained" size="small" sx={{ marginRight: "10px" }} startIcon={<ArrowForwardIcon />}>
                    {translate("Detalles")}
                    </Button>
                    <Button color="primary" target="_blank" variant="contained" href="https://mauri0796.pythonanywhere.com/" size="small" startIcon={<PlayCircleOutlineIcon />}>
                    {translate("DemostracionVivo")}
                    </Button>
                    <Button color="secondary"  target="_blank" variant="contained" href="https://github.com/Mauricio9203/CountriesApp" size="small" startIcon={<GitHubIcon />}>
                        Github
                    </Button>
                </CardActions>
            </Card>
        </Grid>
      </Grow>
        <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Fade in={open} timeout={500}>
            <div style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', width: "80%" }}>
            <Paper sx={{width: "100%"}}>
                <Grid item md={12} xs={12} >
                    <Card sx={{ backgroundColor: darkMode ? "#2E2E2E" : "white", color: darkMode ? "white" : "black" }}>
                        <CardContent>
                            <Typography variant="h6" component="div" >
                            {translate("NombreProyecto_CountriesApp")}
                            </Typography>
                            <Divider/>
                            <Typography variant="body2" mt={2}>
                                {translate("DescripcionProyecto_CountriesApp")}
                            </Typography>
                            <Typography variant="body2" mt={1}>
                                {translate("DescripcionProyecto_CountriesAppDos")}
                            </Typography>
                        </CardContent>
                        <Button onClick={handleClose} color='error' fullWidth>{translate("Cerrar")}</Button>
                    </Card>
                </Grid>
            </Paper>
            </div>
        </Fade>
        </Modal>
    </>
  );
}

export default CountriesApp;
